body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

header {
  background-color:	#331900;
  height: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color:white;
  padding: 20px;
  margin: 10px;
}

.App-link {
  color: #61dafb;
}

.App-nav ul{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  list-style: none;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.App-nav ul li {
  padding: 10px;
}

.About-us {
  margin: 10px;
  color:black;
  background-image: url(/Earthquake/static/media/ground_crack.3aa880a2.svg);
  padding:20px;
}

.About-us p, h1 {
  font-weight:bold;
  text-align: left;
}

.App-build-plan {
  background-color: #FFE7CC;
  margin: 10px;
  padding:20px;
}

.card-group {
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.card {
  margin: 20px;
}

.App-scenarios {
  background-color:#FBC587;
  margin: 10px;
  padding:20px;
}

.scenario-group {
  margin:50px;
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.scenario {
  background-color:white;
  border-radius:50%;
  width: 200px;
  height: 200px;
  line-height: 175px;
  text-align: center;
}





